import React from "react";
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import slid1 from '../../assets/images/istockphoto-1205511045-612x612.jpg'
import slid2 from '../../assets/images/slider-image-1.jpeg'
import slid3 from '../../assets/images/slider-image-3.jpeg'
import slid4 from '../../assets/images/slider-image-2.jpeg'
export default function MainSlider(){
    return(
      <div className="row g-0 overflow-hidden pt-5">
       <div className="col-md-9">
<OwlCarousel className='owl-theme item'items={1}  loop autoplay={true} autoplayTimeout={3000}>
     <img src={slid1} className="w-100"></img>
      <img src={slid2} className="w-100"></img>
       <img  src={slid3} className="w-100"></img>
        <img  src={slid4} className="w-100"></img>
    
</OwlCarousel>
       </div>
       <div className="col-md-3 item2">
       <img  src={slid2} className="w-100"></img>
       <img  src={slid3} className="w-100"></img>
       </div>
      </div>
    );
}