
import axios from "axios";
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
export default function CategorySlider(){
    let[loading,setLoading]=useState(false);
    let baseUrl= 'https://route-ecommerce.onrender.com';
    let [categroislist,setCategroislist]=useState([])

    useEffect(()=>{
        Categorypage()
    },[])


async function Categorypage(){
    setLoading(true)
    let {data} = await axios.get(`${baseUrl}/api/v1/categories`)
    setCategroislist(data.data)
    setLoading(false)
 }

 return<>
      {loading?( <div className="loader-container w-100 h-100">
      	  <div className="spinner"></div>
        </div>)
        :<div className="pt-5 row bg-light  g-md-5 g-3">
     {categroislist.map((el)=>{
        return<div key={el._id} className="col-md-3 col-6  ">
         <Link to={'/categoryDetails/'+el._id}>
            <div className=" text-center product ">
            <div className="bg-white text-center rounded-2">
           <img height={300} src={el.image}  className="w-100 text-denger"/>
           <h5>{el.name}</h5>
           </div>
            </div>
          
          
         
         </Link>
         </div>

       })}
      
        </div>}
    </>;
}

