import React, { useState } from "react";
import { useFormik } from "formik";
import * as Yup from "yup";
import axios from "axios";
import { Link, useNavigate, useParams } from "react-router-dom";

export default function Checkout() {
    let{id}=useParams()
  let [error, setErrorMsg] = useState("");
  let [loading, setloading] = useState(false);
  let navigete = useNavigate();

  let baseUrl = "https://route-ecommerce.onrender.com";
  let validate = Yup.object({
    details: Yup.string().required(),
    phone:Yup.string().required().matches(/^(010|011|012|015)[0-9]{8}$/ ,'Enter valid phone number'),
    city:Yup.string().required().min(2,'main character is 2').max(25,'max character is 25'),
  });

  let formik=useFormik({
    initialValues:{
        details: "",
        phone: "",
        city: "",
    },
    onSubmit:(value)=>{
        
        checkout(value,id)
        console.log(id);
    },
    validationSchema:validate,
 })
  async function checkout( value,id){
let body={
    shippingAddress:value
}
let headers={
    token:localStorage.getItem('token')
}    
setloading(true)
    let{data}=await axios.post(`${baseUrl}/api/v1/orders/checkout-session/${id}?url=http://localhost:3000/#/`,body,{headers}).catch( (error)=>{
        setErrorMsg(error.response.data.message);
        setloading(false)
      })
      setloading(false)
    if(data.status =='success'){
        window.open(data.session.url,'_self')
    }

 }




  return<>
     <div className=" py-5 bg-light">
    <h1 className="h3 bg ">CheckOut </h1>
    <form onSubmit={formik.handleSubmit}>
        <div className="my-3">
        <input onChange={formik.handleChange} type="text" name="details" className="form-control p-3" id="details" placeholder="Details"></input>
        <p className="text-danger">{formik.errors.details}</p>
        </div>
        <div className="my-3">
        <input onChange={formik.handleChange} type="text" name="phone" className="form-control p-3" id="phone" placeholder="Phone"></input>
        <p className="text-danger">{formik.errors.phone}</p>
        </div>
        <div className="my-3">
        <input onChange={formik.handleChange} type="text" name="city" className="form-control p-3" id="city" placeholder="City"></input>
        <p className="text-danger">{formik.errors.city}</p>
        </div>

        {error!="" ?  <div className=" alert alert-danger">{error}</div> :  ""}
        {loading ? <button type="button" className="btn my-3"style={{backgroundColor:' #0e77bb ',color:'white'}} ><i className="fa-solid fa-spinner fa-spin text-white"></i>
        </button> 
        :<button disabled={!formik.isValid} type="submit" className="btn my-3" style={{backgroundColor:' #0e77bb ',color:'white'}}>Pay</button> }
        
       
       
    </form>
    </div>
  </>
}
