import axios from "axios";
import './Home.css'
import React, { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import MainSlider from "../MainSlider/MainSlider";
import CategorySlider from "../CategorySlider/CategorySlider";


export default function Home(){
    let[loading,setLoading]=useState(false);
    let baseUrl= 'https://route-ecommerce.onrender.com';
    let [productslist,setproductslist]=useState([])
    let navigete=useNavigate()

    async function getAllProductsList(){
        setLoading(true)
     let {data} = await axios.get(`${baseUrl}/api/v1/products`)
     setproductslist(data.data)
     setLoading(false)
    }
    useEffect(()=>{
        getAllProductsList()
    },[])

    async function addItemCart(idProduct){
        let body={productId:idProduct}
    let{data}= await axios.post(`${baseUrl}/api/v1/cart`,body,{
        headers:{token:localStorage.getItem('token')}
    })
    console.log(data)
    if(data.status=='success'){
        navigete('/cartDetails')
    }

    }

    return(
        <>
        {loading?( <div className="loader-container w-100 h-100">
      	  <div className="spinner"></div>
        </div>
        ):<>
        <MainSlider></MainSlider>
        <CategorySlider></CategorySlider>
       
        :<div className="row g-md-5 g-3">
        {productslist.map((product)=>{
            return<div key={product._id} className="col-md-3 col-6  product ">
                <Link to={"/productDetails/" +product._id}>
                <div className=" bg-white rounded-4">
               <img src={product.imageCover} className="w-100  rounded-4" alt="product"/>
               <div className="p-2">
               <h2 className="h6 fw-bolder fs-4">{product.title.split(" ").slice(0,2).join(" ")}</h2>
               <p className="fw-lighter" style={{color:'#0e77bb'}}>{product.category[0]?.name}</p>
               <div className="d-flex justify-content-between">
               <p>{product.price}  <span className="fw-bold">EGP</span></p>
               <div>
                <i className="fa-solid fa-star text-warning"></i>{product.ratingsAverage}
               </div>
               </div>
               </div>
            
                </div>
                
                </Link>
                <div className=" prodlayer ">
                <button onClick={()=>{addItemCart(product._id)}} style={{backgroundColor:'#f68b1e'}} className="btn w-100 text-white"> Add Cart <i className="fa-solid fa-cart-plus"></i></button>
                </div>
            </div>
        })}
        </div>
        
        </>
        }

        </>
       
    );
}