import React, { useContext, useEffect, useState } from "react";
import { CartContext } from "../../Share/Cart";
import { Link } from "react-router-dom";

export default function CartDetails() {
    let[loading,setloading]=useState(false);
  let { getAllCartData, CartData,removeItems,updateQuantitiy,loading2,loading1} = useContext(CartContext);
  useEffect(() => {
    setloading(true)
    getAllCartData();
    setloading(false)
    // console.log(CartData)
  }, []);
  return <>
   {loading?( <div className="loader-container w-100 h-100">
      	  <div className="spinner"></div>
        </div>
        ):<>
  {CartData?
  <table className="mt-5 pt-5">
  <thead>
    <tr>
      <th scope="col">Image</th>
      <th scope="col">Name</th>
      <th scope="col">Quantity</th>
      <th scope="col">Price</th>
      <th scope="col">Action</th>
    </tr>
  </thead>
  <tbody>
    {CartData.data?.products.map((el,index)=>{
       return<tr key={index}>
       <td ><img src={el.product?.imageCover} className="w-75" style={{objectFit:'contain'}} height={150} alt="product"></img></td>
       <td >{el.product.title}</td>
       <td >
         <button className="btn btn-danger btn-sm rounded cursor" onClick={()=>{updateQuantitiy(el.product._id,el.count+=1)}}> +</button>
         <span className="mx-3">{el.count}</span>
         <button className="btn btn-success btn-sm rounded" onClick={()=>{updateQuantitiy(el.product._id,el.count-=1)}}> -</button>
       </td>
       <td>{el.price} EGP</td>
       <td>
        : <i onClick={()=>removeItems(el.product._id)} className="fa-solid fa-trash text-danger cursor btn "> </i>
       </td>
     </tr>
    })}
    
    <tr className="">
     <td colSpan={4} className="border fw-bold">Total</td>
     <td>{CartData?.data?.totalCartPrice} EGP</td>
    </tr>
  
  </tbody>
</table>:""}
<Link to={"/checkout/"+ CartData?.data._id} className="btn btn-success my-4">CheckOut Payment</Link>
  </>
  }
   </>
  
}
