import axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import { Carousel } from "bootstrap";
import { CartContext } from "../../Share/Cart";

export default function ProductDetails(){
    let navigete=useNavigate()
    let {id} = useParams()
    let baseUrl= 'https://route-ecommerce.onrender.com';
    let [productDetails,setproductDetails]=useState(null)


    async function getProductDetails(){
     let {data} = await axios.get(`${baseUrl}/api/v1/products/${id}`)
     console.log(data.data)
     setproductDetails(data.data)

    }
    useEffect(()=>{
        getProductDetails();
      
    },[])

    async function addItemCart(idProduct){
        let body={productId:idProduct}
    let{data}= await axios.post(`${baseUrl}/api/v1/cart`,body,{
        headers:{token:localStorage.getItem('token')}
    })
    console.log(data)
    if(data.status=='success'){
        navigete('/cartDetails')
    }

    }

    return(

<div>
    {productDetails? 
    <div className=" row align-items-center my-5 bg-white rounded-2 ">
       <div className="col-md-6">
       <OwlCarousel className='owl-theme' items={1}  loop > 
        {productDetails.images?.map((el ,key) => {return( < img src={el} key={key} className="w-100 pt-5  rounded-4 "/>);})}
     </OwlCarousel>
        
       </div>
       <div className="col-md-6">
       <span className="fw-bold fs-4" style={{color:'#0e77bb'}}>{productDetails.brand?.name}</span>
       <p className="fw-lighter fs-5" style={{color:'#0e77bb'}}>{productDetails.category?.name}</p>
        <h2>{productDetails.title}</h2>
        <p className="text-muted">{productDetails.description}</p>
        {/* <span style={{color:'#0e77bb'}}>{productDetails.title}</span> */}
        
        <div className="d-flex justify-content-between pe-3 pt-3">
               <p> <span className="fw-bold ">Price: </span>{productDetails.price}  <span className="fw-light ">EGP</span></p>
               <div>
                <i className="fa-solid fa-star text-warning"></i>{productDetails.ratingsAverage}
               </div>
        </div>
        <button onClick={()=>{addItemCart(productDetails._id)}} style={{backgroundColor:'#f68b1e'}} className="btn w-100 text-white"> Add Cart <i className="fa-solid fa-cart-plus"></i></button>
       </div>
       
     </div>:""}
   
</div>
    );
}
