import React, { useState } from "react";
import { useFormik, } from "formik";
import *as Yup from'yup';
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";

export default function ResetPassword(){
let[error,setErrorMsg]=useState('');
let[loading,setloading]=useState(false);
let navigete =useNavigate()

let baseUrl= 'https://route-ecommerce.onrender.com';
 let validate=Yup.object({
    email:Yup.string().required().email('Enter valid eamil'),
    newPassword:Yup.string().required().matches(/^[a-zA-Z0-9!@#$%^&*()_-]{6,16}$/,'Enter valid password  '),
 })

 let formik=useFormik({
    initialValues:{
        email:'',
        newPassword:'',
    },
    onSubmit:(value)=>{
        resetPasswordApi(value);
    },
    validationSchema:validate,
 })
  async function resetPasswordApi(objvalu){
    setloading(true)
    let {data}= await axios.put(`${baseUrl}/api/v1/auth/resetPassword`,objvalu).catch((error)=>{
        setloading(false)
        setErrorMsg(error.response.data.message);
        console.log(error)
    })
    setloading(false)
    if(data.token){
        navigete('/login')
    }
    console.log(data)

  }

    return(
        <div>
        <form onSubmit={formik.handleSubmit}>
        <div className="my-3">
        <input onChange={formik.handleChange} type="email" name="email" className="form-control p-3" id="email" placeholder="Email"></input>
        <p className="text-danger">{formik.errors.email}</p>
        </div>
        <div className="my-3">
        <input onChange={formik.handleChange} type="password" name="newPassword" className="form-control p-3" id="newPassword" placeholder="newPassword"></input>
        <p className="text-danger">{formik.errors.newPassword}</p>
        </div>
        {error!="" ?  <div className=" alert alert-danger">{error}</div> :  ""}
        {loading ? <button type="button" className="btn my-3"style={{backgroundColor:' #0e77bb ',color:'white'}} ><i className="fa-solid fa-spinner fa-spin text-white"></i>
        </button> 
        :<button type="submit" className="btn my-3" style={{backgroundColor:' #0e77bb ',color:'white'}}>Update Password</button> }
        
       
       
    </form>

        </div>
      
    )
}