import { createContext, useEffect, useState } from "react";
import axios from "axios";
export let CartContext=createContext(null)


export function CartContextProvider({children}){
  let [CartData,setCartData] = useState()
  let baseUrl= 'https://route-ecommerce.onrender.com';
  let[loading1,setloading1]=useState(false);
  let[loading2,setloading2]=useState(false);
  useEffect(()=>{
    getAllCartData()
  },[])
  async function getAllCartData(){
   let {data}= await axios.get(`${baseUrl}/api/v1/cart`,{
    headers:{token:localStorage.getItem('token')}
})
setCartData(data)

  }

 async function removeItems(id){
  setloading1(true)
  let {data}= await axios.delete(`${baseUrl}/api/v1/cart/${id}`,{
    headers:{token:localStorage.getItem('token')}
})
setloading1(false)
setCartData(data)
 }

 async function updateQuantitiy(id,count){
  setloading2(true)
  let body={count:count}
  let {data}= await axios.put(`${baseUrl}/api/v1/cart/${id}`,body,{
    headers:{token:localStorage.getItem('token')}
})
setloading2(false)
console.log(data)
setCartData(data)
 }




  return <CartContext.Provider value={{getAllCartData,CartData,removeItems,updateQuantitiy,loading2,loading1}}>
    {children}
  </CartContext.Provider>

}