import axios from "axios";
import React, { useEffect, useState,  } from "react";
import { Link, useNavigate, useParams } from "react-router-dom";

export default function BrandProducts(){
    let[loading,setLoading]=useState(false);
    let navigete=useNavigate()
   const {id} =useParams();
    let baseUrl= 'https://route-ecommerce.onrender.com';
    let [brandProducts,setbrandProducts]=useState([])
      useEffect(()=>{
        setLoading(true);
        setTimeout(() => {
          setLoading(false);
        }, 500);
        getBrandProducts()
      },[])

      async function addItemCart(idProduct){
        let body={productId:idProduct}
    let{data}= await axios.post(`${baseUrl}/api/v1/cart`,body,{
        headers:{token:localStorage.getItem('token')}
    })
    console.log(data)
    if(data.status=='success'){
        navigete('/cartDetails')
    }}


     async function getBrandProducts() {
        try {
            setLoading(true)
            let{data}= await axios.get(`${baseUrl}/api/v1/products`,{
                params:{'brand':id}
               }) 
               setbrandProducts(data.data)
               setLoading(false)
               console.log(data.data);
        } catch (error) {
            setLoading(false)
           console.log("Error: " + error) 
        }
     
    }

    return<>
    {loading? (
        <div className="loader-container w-100 h-100">
      	  <div className="spinner"></div>
        </div>) 

      : <div className="row bg-light pt-5 g-md-5 g-3">
      {brandProducts.length==0?<div className="fs-1 fw-bold text-center pt-5 "><h1  style={{color:'#0e77bb'}}>Sorry No Products available Now...</h1></div>
      :brandProducts.map((product,index)=>{
        return<>
       <div key={index} className="col-md-3 col-sm-6   product ">
        <Link to={"/productDetails/" +product._id}>
        <div className=" bg-white rounded-4">
       <img src={product.imageCover} className="w-100  rounded-4" alt="product"/>
       <div className="p-2">
       <span style={{color:'#0e77bb'}}>{product.category[0]?.name}</span>
       <p style={{color:'#0e77bb'}}>{product.brand.name}</p>

       <h2 className="h6 fw-bold">{product.title.split(" ").slice(0,2).join(" ")}</h2>
       <div className="d-flex justify-content-between">
       <p> Price:  {product.price}  <span className="fw-bold">EGP</span></p>
       <div>
        <i className="fa-solid fa-star text-warning"></i>{product.ratingsAverage}
       </div>
       </div>
     
       </div>
      
       
        </div>
        
        </Link>
        <div className=" prodlayer ">
        <button onClick={()=>{addItemCart(product._id)}}  style={{backgroundColor:'#f68b1e'}} className="btn w-100 text-white"> Add Cart <i className="fa-solid fa-cart-plus"></i></button>
        </div>
        
    </div>
        </>
     


    })
      }  
   
         </div> }
   
    
    
    </>
}