import axios from "axios";
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";

export default function Brands(){
    let[loading,setLoading]=useState(false);
    let baseUrl= 'https://route-ecommerce.onrender.com';
    let [brandslist,setbrandslist]=useState([])

    useEffect(()=>{

        setLoading(true);
        setTimeout(() => {
          setLoading(false);
        }, 500);
        Brandspage()
    },[])


async function Brandspage(){
    
    setLoading(true)
    let {data} = await axios.get(`${baseUrl}/api/v1/brands`)
    setbrandslist(data.data)
    setLoading(false)
 }

 return<>
{loading?( <div className="loader-container w-100 h-100">
      	  <div className="spinner"></div>
        </div>
        )
        :<div className="pt-5 row bg-light  g-md-5 g-3">
{brandslist.map((el)=>{
   return<div key={el._id} className="col-md-3  col-sm-6 col-12  product ">
    <Link to={'/brandsProducts/'+el._id}>
       <div className="text-center shadow-sm">
       <div className="bg-white text-center rounded-3 ">
        <img height={200} src={el.image}  className="w-100 text-denger"/>
       </div>
    
      <h5 style={{color:'#0e77bb'}}>{el.name}</h5>
      </div>
    
    </Link>
    </div>

  })}
 
</div>
}
  
   </>;
}