import React from "react";
// import Nav from 'react-bootstrap/Nav';
// import Navbar from 'react-bootstrap/Navbar';

import { Link, NavLink } from 'react-router-dom'
import './Navbar.css'
import logo from "../../assets/images/imlo.jpeg"
import { useContext } from "react";
import { CartContext } from "../../Share/Cart";

export default function Navbar({userData,logOut}){
  let {CartData, removeItems,updateQuantitiy} = useContext(CartContext);
    return(
      <>
    
  <nav className="navbar navbar-expand-lg ">
      <div className="container">
          <Link className="navbar-brand" to="/">
           <img src={logo} className="col-4" alt="logo"/>
           <span className="fw-bolder " style={{color:'#0e77bb', fontSize:"1.8rem"}}> <span style={{fontSize:"2rem" ,fontFamily:'sans-serif'}}>F</span>reshCart</span>
          </Link>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            {userData?
             <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                
                  <li className="nav-item px-2">
                      <NavLink  className={({ isActive }) => isActive ? 'nav-link active' : 'nav-link'} to='home'>Home</NavLink>
                  </li>
        
                  <li className="nav-item px-2">
                      <NavLink className={({ isActive }) => isActive == true ? 'nav-link active' : 'nav-link'} to="Categorypage" >Category</NavLink>
                  </li>
                  <li className="nav-item px-2">
                      <NavLink className={({ isActive }) => isActive == true ? 'nav-link active' : 'nav-link'} to="brands" >Brands</NavLink>
                  </li>
              </ul> 
              :""}
             
              <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                {userData?
                  <>
       
               <li className="nav-item px-2">
                      <NavLink  className={({ isActive }) => isActive ? 'nav-link active' : 'nav-link'} to='profile'>Profile</NavLink>
                  </li>


                  <li className="nav-item px-2" data-bs-toggle="offcanvas" data-bs-target="#offcanvasRight" aria-controls="offcanvasRight">
                      <span  className= 'nav-link'>
                      <div className=" position-relative">
                      <i className="fa-solid fa-cart-shopping"></i>
                     <span className="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger">
                          {CartData?.numOfCartItems}
                         </span>
                      </div>
                      </span>
                  </li>


               <li  className="nav-item px-2">
              <span className="nav-link" onClick={logOut}>LogOut</span>
                 </li>
                  </>
                  :
                 <>
                <li className="nav-item px-2">
               <NavLink  className={({ isActive }) => isActive ? 'nav-link active' : 'nav-link'} to='login'>Login</NavLink>
                </li>

                <li className="nav-item px-2">
            <NavLink className={({ isActive }) => isActive == true ? 'nav-link active' : 'nav-link'} to="/" >Register</NavLink>
                </li>
                </>}
        
       
        

       
        
       
             </ul>
        
          </div>
      </div>
  </nav>
 

<div className="offcanvas offcanvas-end" tabindex="-1" id="offcanvasRight" aria-labelledby="offcanvasRightLabel">
  <div className="offcanvas-header">
    <h5 className="offcanvas-title fw-bold "style={{color:'#0e77bb'}} id="offcanvasRightLabel">Cart Details</h5>
    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
  </div>
  <div className="offcanvas-body">
    <>
  
    {CartData?
    CartData.data.products.map((el,ind)=>{
     return  <div className="shadow-sm my-1" key={ind}>

     <div className="d-flex justify-content-between align-items-center ">
       <img src={el.product.imageCover} style={{objectFit:'contain'}} height={80} className="w-25" alt=""></img>
       <div>
       <button className="btn btn-danger btn-sm rounded cursor"  onClick={()=>{updateQuantitiy(el.product._id,el.count+=1)}}> +</button>
        <span className="mx-3">{el.count}</span>
        <button className="btn btn-success btn-sm rounded"  onClick={()=>{updateQuantitiy(el.product._id,el.count-=1)}} > -</button>
       </div>
       <div>
       <i onClick={()=>removeItems(el.product._id)} className="fa-solid fa-trash text-danger cursor btn "> </i>
       </div>

     </div>
     <h6>{el.product.title.split(" ").slice(0,4).join(" ")}</h6>

   </div>
    })
    :""}
</>
  
  </div>
  <div className="offcanvas-bottom">
  <div className="d-flex justify-content-between align-content-center py-2 px-3 border rounded-2">
    <h3 className=" fw-bold" style={{color:'#0e77bb'}}>Total</h3>
    <h5 className="fw-bold">{CartData?.data.totalCartPrice} EGP</h5>
   </div>

  <Link to={"/home/"} className="btn btn-success my-1  w-100" style={{backgroundColor:'#0e77bb'}}>Add More Item</Link>
  <Link to={"/checkout/"+ CartData?.data._id} className="btn btn-success my-1  w-100">CheckOut Payment</Link>
  </div>
</div>
  </>
    );
}
