
import axios from "axios";
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
export default function CategorySlider(){
    let baseUrl= 'https://route-ecommerce.onrender.com';
    let [categroislist,setCategroislist]=useState([])

    useEffect(()=>{
        getcategroislist()
    },[])

    async function getcategroislist(){
      
     let {data} = await axios.get(`${baseUrl}/api/v1/categories`)
       console.log(data.data)
     setCategroislist(data.data)
    }
   
    return(
      <div className="p-1">
         
          <OwlCarousel className="owl-theme col-md-12  " loop items={5} autoPlay={true} autoplayTimeout={2000} dots={true}> 
        
        {categroislist.map((el)=>{
           return<div key={el._id} className=" ">
            <Link to={'/categoryDetails/'+el._id}>
              <div className="rounded-5 product shadow text-center  justify-content-center">
              <img height={220} style={{objectFit:'contain'}} src={el.image}  className="w-75 text-denger"/>
              <h5>{el.name}</h5>
              </div>
            
            </Link>
            </div>

          })}

        </OwlCarousel>
         
      </div>
    )
}