import React, { useEffect, useState } from 'react';
import { Navigate, RouterProvider, createHashRouter } from 'react-router-dom';
import Layout from './Component/Layout/Layout';
import Home from './Component/Home/Home';
import Products from './Component/Prorducts/Products';
import Profile from './Component/Profile/Profile';
import Login from './Component/Login/Login';
import Register from './Component/Register/Register';
import NotFound from './Component/NotFound/NotFound';

import jwtDecode from 'jwt-decode';
import ProtectedRouting from './Component/ProtectedRouting';
import ForgetPassword from './Component/ForgetPassword/ForgetPassword';
import ResetPassword from './Component/ForgetPassword/ResetPassword';
import ProductDetails from './Component/ProductDetails/ProductDetails';
import CategoryDetails from './Component/CategorySlider/CategoryDetails';
import Categorypage from './Component/CategorySlider/Categorypage';
import Brands from './Component/Brand/Brands';
import BrandProducts from './Component/Brand/BrandProducts';
import CartContext, { CartContextProvider } from './Share/Cart';
import CartDetails from './Component/Cart/CartDetails';
import Checkout from './Component/Checkout/Checkout';
 export default function App() {
   // current user
let [userData,setUserData]=useState(null);


useEffect(()=>{
    
  if(localStorage.getItem('token') !=null ){
    //decode the token user data
    const data=jwtDecode(localStorage.getItem('token'))
    //set current user data
    setUserData(data)
    saveUserData(data);
  }
},[])


// get user data
function saveUserData(userData) {
  //send currnt user data
  setUserData(userData)
}

function logOut(){
  setUserData(null);
  localStorage.removeItem('token');
  return <Navigate to='login'/>
}

function Protected2(props){
  if(localStorage.getItem('token')!=null){
    return <Navigate to='home'/>
  }else{
    return props.children
  }

}

  let routes=createHashRouter([
    {
    
      path:"" ,element: <Layout logOut={logOut} userData={userData}/>,children:[
        { path: "home" ,element:<ProtectedRouting><Home/></ProtectedRouting> },
        { path: "Categorypage" ,element:<ProtectedRouting> <Categorypage/> </ProtectedRouting> } ,
        { path: "brands" ,element:<ProtectedRouting> <Brands/> </ProtectedRouting> } ,
        { path: "profile" ,element:<ProtectedRouting> <Profile userData={userData}/></ProtectedRouting> },
        { path: "productDetails/:id" ,element: <ProtectedRouting><ProductDetails /> </ProtectedRouting>},
        { path: "checkout/:id" ,element: <ProtectedRouting><Checkout /> </ProtectedRouting>},
        { path: "categoryDetails/:id" ,element: <ProtectedRouting><CategoryDetails/> </ProtectedRouting>},
        { path: "brandsProducts/:id" ,element: <ProtectedRouting><BrandProducts/> </ProtectedRouting>},
        { path: "cartDetails" ,element: <ProtectedRouting><CartDetails/> </ProtectedRouting>},
        { path: "login" ,element: <Login saveUserData={saveUserData} />},
        { path: "ForgetPassword" ,element: <ForgetPassword />},
        { path: "resetPassword" ,element: <ResetPassword />},
      
        { index:true ,element:<Protected2> <Register/> </Protected2>},
        { path: "*" ,element: <NotFound/>},

      ]
    }
   ])

  return (
  <CartContextProvider>
 <RouterProvider router={routes}/>
 </CartContextProvider>
  
 
  )
}


