import React, { useState } from "react";
import './Register.css'
import { useFormik, validateYupSchema } from "formik";
import *as Yup from'yup';
import axios from "axios";
import { useNavigate } from "react-router-dom";

export default function Register(){
let[error,setErrorMsg]=useState('');
let[loading,setloading]=useState(false);
 let navigete =useNavigate()

let baseUrl= 'https://route-ecommerce.onrender.com';
 let validate=Yup.object({
    name:Yup.string().required().min(2,'main character is 2').max(15,'max character is 15'),
    email:Yup.string().required().email('Enter valid eamil'),
    phone:Yup.string().required().matches(/^(010|011|012|015)[0-9]{8}$/ ,'Enter valid phone number'),
    password:Yup.string().required().matches(/^[a-zA-Z0-9!@#$%^&*()_-]{6,16}$/,'Enter valid password  '),
    rePassword:Yup.string().required().oneOf([Yup.ref('password')],'Re-Password is wrong !!'),
 })

 let formik=useFormik({
    initialValues:{
        name:'',
        email:'',
        password:'',
        rePassword:'',
        phone:'',
    },
    onSubmit:(value)=>{
        sendDataRegister(value);
    },
    validationSchema:validate,
 })


  async function sendDataRegister(objData){
    setloading(true)
  let {data}= await axios.post(`${baseUrl}/api/v1/auth/signup`,objData).catch( (error)=>{
    setErrorMsg(error.response.data.message);
    setloading(false)
  })
  setloading(false)
  if(data.message== "success"){
    //login
    navigete('/login')
  }

 }



    return(<>
    <div className=" py-5 bg-light">
    <h1 className="h3 bg ">Register Now :</h1>
    <form onSubmit={formik.handleSubmit}>
  
        <div className="my-3">
        <input onChange={formik.handleChange} type="text" name="name" className="form-control p-3" id="name" placeholder="Name"></input>
        <p className="text-danger">{formik.errors.name}</p>
        </div>
        <div className="my-3">
        <input onChange={formik.handleChange} type="email" name="email" className="form-control p-3" id="email" placeholder="Email"></input>
        <p className="text-danger">{formik.errors.email}</p>
        </div>
        <div className="my-3">
        <input onChange={formik.handleChange} type="password" name="password" className="form-control p-3" id="password" placeholder="Password"></input>
        <p className="text-danger">{formik.errors.password}</p>
        </div>
        <div className="my-3">
        <input onChange={formik.handleChange} type="password" name="rePassword" className="form-control p-3" id="rePassword"placeholder="rePassword"></input>
        <p className="text-danger">{formik.errors.rePassword}</p>
        </div>
        <div className="my-3">
        <input onChange={formik.handleChange} type="text" name="phone" className="form-control p-3" id="phone"placeholder="Phone"></input>
        <p className="text-danger">{formik.errors.phone}</p>
        </div>
        {error!="" ?  <div className=" alert alert-danger">{error}</div> :  ""}
        

        {loading ? <button type="button" className="btn my-3"style={{backgroundColor:' #0e77bb ',color:'white'}} ><i className="fa-solid fa-spinner fa-spin text-white"></i>
        </button> 
        :<button disabled={!formik.isValid} type="submit" className="btn my-3" style={{backgroundColor:' #0e77bb ',color:'white'}}>Register</button> }
        
       
       
    </form>
    </div>
        
        </>
    );
}